.animate {
  font-size: 50px;
  font-weight: 700;
  position: absolute;
  z-index: 1;
  transition: transform 10s;
  background: linear-gradient(13deg, #ff8f5a 26%, #ff2f96 72%);
  -webkit-background-clip: text;
  color: transparent; }

.animate_1 {
  left: 10%;
  top: 10%; }

.animate_2 {
  left: 40%;
  top: 50%; }

.animate_3 {
  left: 5%;
  top: 68%; }

.animate_4 {
  left: 60%;
  top: 20%; }

.animate_5 {
  left: 63%;
  top: 50%; }

.main__aboutme:hover .animate_1 {
