/* Добавляем этот класс HTML  */
.popup__html_fixed {
  overflow: hidden; }

/* Добавляем этот класс BODY  */
.popup__body_fixed {
  overflow: scroll; }

.popup {
  width: 500px;
  border-radius: 3px;
  background: #fff;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  opacity: 0;
  z-index: 10; }

.popup__form {
  z-index: 9;
  position: fixed;
  background-color: #000;
  opacity: 0.85;
  filter: alpha(opacity=80);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: none; }

.popup__close {
  position: absolute;
  font-size: 18px;
  right: -20px;
  top: -20px;
  color: #fff;
  cursor: pointer; }

.popup__header {
  margin: 20px auto 40px;
  text-align: center; }

.popup__form_label {
  width: 300px;
  margin: 20px auto;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.popup__form_input {
  width: 200px;
  height: 30px;
  padding: 0 10px;
  border: 1px solid #808080;
  border-radius: 2px; }

.popup__form_input:focus {
  border: 1px solid #ff2f96; }

.popup__form_pattern {
  color: #F00; }

.popup__button {
  display: block;
  border: none;
  border-radius: 2px;
  background: linear-gradient(13deg, #ff8f5a 26%, #ff2f96 72%);
  color: #fff;
  width: 268px;
  height: 60px;
  cursor: pointer;
  margin: 40px auto 20px; }

.popup__alert {
  min-width: 300px;
  border-radius: 3px;
  background: #fff;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  opacity: 0;
  z-index: 12; }

.popup__alert_header {
  text-align: center;
  margin: 20px 50px 40px; }

.popup__alert_text {
  text-align: center;
  margin: 20px 50px 40px;
  line-height: 22px; }

.error {
  border: 1px solid #f00;
  color: red; }

@media (max-width: 624px) {
  .popup {
    width: 300px;
    top: 40%; }
  .popup__form_label {
    width: 200px;
    flex-direction: column;
    align-items: flex-start; }
  .popup__header {
    margin: 60px auto 40px; }
  .popup__button {
    width: 200px;
    margin: 40px auto 20px; }
  .popup__alert {
    top: 40%; }
  .popup__alert_header {
    margin: 20px 30px 40px; }
  .popup__close {
    right: 8px;
    top: 8px;
    color: red; } }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  font-family: Roboto, sans-serif; }

a {
  text-decoration: none;
  color: #000; }

ul {
  list-style: none; }

.conteiner {
  max-width: 1210px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px; }

@media (max-width: 1824px) {
  .conteiner {
    max-width: 1200px; } }

@media (max-width: 1224px) {
  .conteiner {
    max-width: 1000px; } }

@media (max-width: 1024px) {
  .conteiner {
    max-width: 985px; } }

