  .conteiner {
    max-width: 600px; } }

@media (max-width: 624px) {
  .conteiner {
    max-width: none; } }

.visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden; }

.gradient__background {
  background: linear-gradient(13deg, rgba(255, 143, 90, 0.95) 26%, rgba(255, 47, 150, 0.95) 72%); }

.gradient__text {
  background: linear-gradient(13deg, #ff8f5a 26%, #ff2f96 72%);
  -webkit-background-clip: text;
  color: transparent; }

.button__colir_gradient {
  border: none;
  border-radius: 2px;
  background: linear-gradient(13deg, #ff8f5a 26%, #ff2f96 72%);
  color: #fff; }

.button__orange {
